#wipIcon {
  margin-bottom: 20px;
  max-width: 30px;

  .dashicon,
  i {
    font-size: 30px;

    &:hover {
      cursor: pointer;
      color: #3175d5;
    }
  }
}

.wip-iconpicker-popup {
  width: 350px;
  top: 10px !important;

  .components-search-control__input-wrapper {
    margin: 10px 15px;

    input {
      border: 1px solid #d0d5dd !important;
      background-color: #fff !important;
      border-radius: 8px !important;
    }
  }

  .components-popover__content {
    width: 100%;
  }
}

.wip-parent-tab-panel {
  .components-tab-panel__tabs {
    border-bottom: 1px solid #eaecf0;
  }

  .wip-icon-tab {
    height: 30px;
    color: #9f9d9d;
  }

  .wip-icon-tab,
  .wip-tab {
    &.active-tab {
      border-bottom: 2px solid #3c434a;
      color: #3c434a;
    }
  }
}

.wip-icon-area {
  display: flex;
  flex-wrap: wrap;
  width: calc(100% - 20px);
  gap: 5px;
  overflow-y: scroll;
  overflow-x: hidden;
  align-items: flex-start;
  justify-content: flex-start;
  max-height: 225px;
  margin: 10px 15px;

  .wip-icon-box {
    text-align: center;
    width: calc(20% - 4px);
    height: 50px;
    position: relative;
    transition: 0.3s;
    order: 2;

    &.active {
      background-color: #f2f4f7;
      border-radius: 8px;
      order: 1;
    }

    &:hover {
      background-color: #f2f4f7;
      border-radius: 8px;
      cursor: pointer;

      // .components-panel__row {
      // 	display: block;
      // }
    }

    .wip-icon-content {
      height: 100%;
      padding: 5px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .dashicon,
    i {
      font-size: 22px;
      display: block;
      margin: 0 auto;
    }

    .components-panel__row {
      font-size: 11px;
      line-height: 1.4;
      border-radius: 8px;
      display: block;
      text-transform: capitalize;
      background-color: #101828;
      color: #ffffff;
      height: auto !important;
      min-height: auto !important;
      padding: 10px 5px;
      display: none;
      position: absolute;
      top: calc(100% + 6px);
      width: 130%;
      z-index: 999;

      &:before {
        content: "";
        position: absolute;
        left: calc(50% - 5px);
        top: -6px;
        border-top: none;
        border-right: 6px solid transparent;
        border-left: 6px solid transparent;
        border-bottom: 6px solid black;
      }
    }
  }
}

.wip-social-profile {
  #wipIcon {
    margin-bottom: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100%;
    min-width: 30px;

    .dashicon,
    i {
      font-size: 18px;
    }
  }
}
